.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 500px;
  width: 90%;
  text-align: center;
}

.modal-body {
  margin-bottom: 1px;
}

.modal-footer {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.btn-danger {
  background-color: #dc3545;
  border: none;
  width: auto;
  font-size: 0.9rem;
  padding: 8px 12px;
  cursor: pointer;
}

.btn-danger:hover {
  background-color: #c82333; 
}

@media (max-width: 576px) {
  .modal-content {
    max-width: 350px;
  }

  .modal h3 {
    font-size: 1rem;
  }

  .modal h4 {
    font-size: 0.9rem;
  }

  .btn-danger {
    background-color: #dc3545;
    border: none;
    width: 90%;
    max-width: 250px;
    font-size: 0.9rem; 
    padding: 8px 12px; 
   
  }

  .bola-numero {
    width: 25px;
    height: 25px;
    font-size: 10px;
  }
}
