.titulo {
    margin-top: 80px;
  }
  
  .lead {
    color: #6c757d;
    font-size: 1.2rem;
  }
  
  .row {
    justify-content: center;
    gap: 15px;
  }
  
  .btn-primary-milionaria {
    background-color: #582E56;
    border: none;
    font-size: 1rem;
    font-weight: bold;
    padding: 15px 20px;
    text-transform: uppercase;
    border-radius: 10px;
    transition: all 0.3s ease;
    width: 200px;
  }
  
  .btn-primary-milionaria:hover {
    background-color: #6846B6;
    transform: scale(1.05);
  }

  .logo-container {
     margin-top: -80px;
     margin-bottom: -65px;
}
             
  .milionaria-logo {
     width: 200px;
     height: auto;
     display: block;
     margin: 0 auto;
}